import React from 'react';
import Link from 'next/link';

import {routers} from './routers.js';
import './style.scss';

const Header = props => {
  return (
    <header>
      <h1 className="title">ffutop</h1>
      <div>
        {routers.map(router => (
          <Link
            href={router.path}
            key={router.name}
            as={process.env.PREFIX_PATH + router.path}>
            {router.title}
          </Link>
        ))}
      </div>
    </header>
  );
};

export default Header;
